@import url(https://fonts.googleapis.com/css2?family=Changa:wght@200&family=Jura&family=Monoton&family=Press+Start+2P&family=Train+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}





#header-title {
    font-family: "Press Start 2P", Helvetica, Arial, sans-serif;
    text-transform: uppercase;
    font-size: 250%;
    text-shadow: 2px 0 0 #000, -2px 0 0 #000, 0 2px 0 #000, 0 -2px 0 #000, 1px 1px #000, -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000;
    color: white;
    transition: 0.7s;
}

#credits {
    color: white;
    font-size: 20px;
    font-style: italic;
}
 #credits a {
     color: black;
     margin-left: 10px;
     color: #dcfda8;
     text-decoration: none;
     font-size: 21px;
     padding: .2em;
     transition: .5s;
     display: inline-block
 }

 #github-account:hover {
     color:#bfff00;
     transition: .5s;
 }
#github-account::after {
  content: "";
  position: relative;
  top: 6px;
  width: 0;
  height: 2px;
  background-color:#bfff00;
  display: block;
  transition: width 0.5s;
}

#github-account:hover::after {
  width: 100%;
}

#header {
    /*position:fixed;*/
    /*top: 0;*/
    /*right: 0;*/
    /*width: 100%;*/
    z-index : 100000;
    position: fixed;
    border-bottom: 1px solid black;
    top: 0;
    margin: 0;
    background: rgb(2,0,36);
    background:linear-gradient(182deg, rgb(143 143 255) 35%, rgba(0,212,255,1) 100%);
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    overflow:  hidden;
    height: 50vh !important;
      width: 100%;
    transition: .7s;
} 

#header.small {
      height: 20vh !important;
      font-size:  16px;
}
#header.small #header-content {
}
 #header.small #header-title {
     font-size: 23px;
 }
@media (max-width: 700px) {
    #header.small {
        height: 20vh !important;
    }
    #header-title {
        font-size: 200%;
    }
}
@media (max-width: 380px) {
    #header-title  {
        font-size:  150%;
    }
}
@media (max-width: 700px) {
    #header {
        -webkit-flex-direction: column;
                flex-direction: column;
        height: 65vh !important;
    }
    #header.small #main-logo{
        display: none;
    }
    #main-logo {
        height:25vw !important;
        width:25vw !important;
    }
}

  #header.small #searchbox {
      font-size: 16px;
  }
  #header.small #searchbox input {
      height: 30px;
  }


  #header #main-logo-container {
      height: 100%;
      display: -webkit-flex;
      display: flex;
      -webkit-align-items: flex-end;
              align-items: flex-end;
  }
  #header #main-logo {
      height: 20vw;
      width: 40vh;
      min-width: 130px;
      min-height: 130px;
      transition: .7s;
  }
  #header.small #main-logo {
      width: 100px;
      height: 100px;
  }


#robot-list {
    margin-top: 50vh;
    transition: .7s;
}

